.edit-button {
  padding: 10px 20px;
  font-size: 10px;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: #007bff;
  border: 2px solid #007bff;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease,
    border-color 0.3s ease;
}

.edit-button:hover {
  background-color: #0056b3;
  border-color: #0056b3;
  border-radius: 5px;
}
.edit-button:active {
  background-color: #004080;
  border-color: #004080;
}

