.card-7 {
  width: 18rem;
  margin-bottom: 20px;
  border: 1px solid #ccc;

  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.card-body {
  display: flex;
  flex-direction: column;
}

.card-row {
  display: flex;
  align-items: center;
  padding: 1rem;
}

.card-title {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
  color: black;
}

.card-text {
  font-size: 1rem;
  color: #333;
  margin-bottom: 0.5rem;
}
.card-7 {
  transition: all 0.5s ease;
}
.card-7:hover {
  transform: scale(1.1);
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}
