.card-cart {
  max-width: 70em; /* Adjust the max-width to increase the card width */
  height: 60%;
  margin-top: 20px;
  margin-bottom: 30px;
  flex-direction: row;
  margin-left: 20px;
  box-shadow: 0px 30px 10px -20px rgba(50, 50, 93, 0.25),
    0px 30px 60px -30px rgba(0, 0, 0, 0.3),
    inset 0px -2px 6px 0px rgba(10, 37, 64, 0.35);
  border: none;
}
.card-cart img {
  max-width: 100%;
  height: 90%;
}
.card-body {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 475) {
  .card {
    font-size: 0.9em;
  }
}
.cart-length {
  color: gray;
  font-size: 20px;
  margin-top: 10px;
}
