.login-button {
  background-color: #3b5d50;
  padding: 5px 20px;
  font-size: 10px;
  text-decoration: none;
  color: #ffffff;
  border: 2px solid #3b5d50;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease,
    border-color 0.3s ease;
}

.login-button:hover {
  background-color: #3b5d50;
  border-color: #3b5d50;
  border-radius: 5px;
}

.forgot {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
