.hero2 {
  background-image: url("../../public/images/Nature%204.jpg");
  padding: calc(4rem - 30px) 0 0rem 0;
  height: 70vh;
  position: relative;
  box-shadow: 0px 30px 10px -20px rgba(50, 50, 93, 0.25),
    0px 30px 60px -30px rgba(0, 0, 0, 0.3),
    inset 0px -2px 6px 0px rgba(10, 37, 64, 0.35);
  background-repeat: no-repeat;
  background-size: cover; /* Ensure the background image covers the entire container */
  background-position: center; /* Center the background image horizontally and vertically */
}
@media (min-width: 768px) {
  .hero3 {
    padding: calc(4rem - 30px) 0 4rem 0;
  }
}
@media (min-width: 992px) {
  .hero3 {
    padding: calc(8rem - 30px) 0 8rem 0;
  }
}
.hero .intro-excerpt {
  position: relative;
  z-index: 4;
}
@media (min-width: 992px) {
  .hero .intro-excerpt {
    max-width: 450px;
  }
}
.hero h1 {
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 30px;
}
@media (min-width: 1400px) {
  .hero h1 {
    font-size: 54px;
  }
}
.hero p {
  color: rgba(255, 255, 255, 0.5);
  margin-botom: 30px;
}
.hero .hero-img-wrap {
  position: relative;
}
.hero .hero-img-wrap img {
  position: relative;
  top: 0px;
  right: 0px;
  z-index: 2;
  max-width: 780px;
  left: -20px;
}
@media (min-width: 768px) {
  .hero .hero-img-wrap img {
    right: 0px;
    left: -100px;
  }
}
@media (min-width: 992px) {
  .hero .hero-img-wrap img {
    left: 0px;
    top: -80px;
    position: absolute;
    right: -50px;
  }
}
@media (min-width: 1200px) {
  .hero .hero-img-wrap img {
    left: 0px;
    top: -80px;
    right: -100px;
  }
}

@media (min-width: 1200px) {
  .hero .hero-img-wrap:after {
    top: -40px;
  }
}
.intro-excerpt-read {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  margin-top: 300px;
}

/* ==================================================================================== */
.card1 {
  display: flex;
  justify-content: center;
}
.card {
  max-width: 70em; /* Adjust the max-width to increase the card width */
  height: 30%;
  margin-top: 20px;
  margin-bottom: 30px;
  flex-direction: row;
  margin-left: 20px;
  box-shadow: 0px 30px 10px -20px rgba(50, 50, 93, 0.25),
    0px 30px 60px -30px rgba(0, 0, 0, 0.3),
    inset 0px -2px 6px 0px rgba(10, 37, 64, 0.35);
  border: none;
}
.card img {
  max-width: 20%;
  border-radius: 0.6em;
}
.card-body {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 475) {
  .card {
    font-size: 0.9em;
  }
}
.team-body {
  background-color: #e6e6e6;
}
