.header-login {
  background-color: #ffc300;
  color: #fff;
  border: none;
  padding: 5px 25px;
  margin-left: 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.header-signup {
  background-color: #ff5733;
  color: #fff;
  border: none;
  padding: 5px 25px;
  margin-left: 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dari {
  margin-top: 10px;
}
.icon {
  margin-right: 3px;
  height: 30px;
}

.search-button {
  margin-left: 10px;
  font-size: 30px;
}
.cart-button {
  margin-left: 10px;
  font-size: 28px;
  color: #ffffff;
}

.nav-link:hover {
  color: white; /* Change text color to white on hover */
  background-color: black;
  cursor: pointer;
  border-radius: 120px;
}

.dropdown-menu .dropdown-item {
  background-color: white;
  color: black;
}

/* Your existing CSS styles */

.dropdown-menu {
  display: none; /* Hide dropdown menu by default */
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
}

.dropdown-menu.show {
  display: block; /* Show dropdown menu when it has the "show" class */
}

.dropdown-menu .dropdown-item {
  background-color: white;
  color: black;
}
