@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

#footer {
  background: white;
  padding: 3rem;
  /* padding-top: 5rem; */
  padding-top: 7rem;
  padding-bottom: 80px;
}
#footer2 {
  background: #f7f7f7;
  padding: 3rem;
  margin-top: 0px;
  /* padding-top: 5rem; */
  padding-top: 7rem;
  padding-bottom: 80px;
}
.font {
  font-family: "Poppins", sans-serif;
}
.social-links h2 {
  font-size: 20px;
  font-weight: 600;
}
.social-links img {
  padding-bottom: 25px;
}
.social-icons {
  /* display: flex;
      gap: 3rem; */
  display: flex;
  flex-direction: column;
  gap: 1rem;
  color: #777777;
}

/*Commented by Muntasirul Islam , muntasirul.msd@gmail.com*/
.social-icons a {
  color: white;
}
.social-icons a:hover {
  letter-spacing: 2px;
}
.social-icons a i {
  box-shadow: white 0px 4px 12px;
  padding: 0.4rem 1rem 0.4rem 1rem;
  border-radius: 3px;
  color: #d56742;
  font-size: 18px;
  margin-right: 12px;
}
li {
  list-style: none;
}
.useful-link h2 {
  font-size: 20px;
  font-weight: 600;
}

.use-links {
  line-height: 32px;
}
.use-links li i {
  font-size: 14px;
  padding-right: 8px;
  color: gray;
}
.use-links li a {
  color: #303030;
  font-size: 18px;
  font-weight: 500;
  color: gray;
}
.use-links li a:hover {
  letter-spacing: 2px;
}
.address h2 {
  font-size: 20px;
  font-weight: 600;
}
.address img {
  padding-bottom: 15px;
}
.address-links li a {
  color: black;
  font-size: 16px;
  font-weight: 500;
  color: white;
}
.address-links li i {
  font-size: 16px;
  padding-right: 8px;
  color: #d56742;
}
.address-links li i:nth-child(1) {
  padding-top: 9px;
}
.address-links .address1 {
  font-weight: 500;
  font-size: 16px;
  display: flex;
  margin-bottom: 2px;
}
.address-links {
  line-height: 20px;
